<template>
  <div
    ref="wrapper"
    class="scroll"
  >
    <slot />
  </div>
</template>

<script>
import BScroll from 'better-scroll';

export default {
  props: {
    data: {
      type: Array,
      default: null
    },
    // 
    probeType: {
      type: Number,
      default: 1
    },
    // 
    listenScroll: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    data(val) {
      this.$nextTick(() => this.refresh());
    }
  },
  mounted() {
    // // DOMbetter-scroll
    this.$nextTick(() => this._initScroll());
  },
  methods: {
    _initScroll() {
      if (!this.$refs.wrapper) {
        return;
      }
      this.scroll = new BScroll(this.$refs.wrapper, {
        probeType: this.probeType,
        scrollY: true, // Y
        click: true, // clickclickbetterscrollclickevent._constructedbstrue
        momentum: true, // 
        bounce: false, // 
        bounceTime: 700, // 
        deceleration: 0.001, // 0.01
        momentumLimitTime: 300, // 
        momentumLimitDistance: 15, // 
        resizePolling: 60 // better-scroll
      });
      // scroll
      if (this.listenScroll) {
        // 
        this.scroll.on('scrollStart', () => this.$emit('scrollStore', true));
        // pos
        this.scroll.on('scroll', (pos) => {
          this.$emit('distance', Math.abs(pos.y));
          this.$emit('scrollStore', true);
        });
        // 
        this.scroll.on('scrollEnd', (val) => {
          this.$emit('scrollStore', false);
        });
      }
    },
    // 
    scrollTo() {
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    // 
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    },
    // 
    refresh() {
      this.scroll && this.scroll.refresh();
    }
  }
};

</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
.scroll
  height 100%
  width 100%
  overflow hidden
  background #eee
</style>



